@font-face {
  font-family: Articul;
  font-style: normal;
  font-weight: 500;
  src: url(./fonts/Articul-Medium.1e29ac4c.woff2) format("woff2");
  font-display: swap;
}
@font-face {
  font-family: Articul;
  font-style: normal;
  font-weight: 400;
  src: url(./fonts/Articul.542bbe19.woff2) format("woff2");
  font-display: swap;
}
@font-face {
  font-family: DrukWide;
  font-style: normal;
  font-weight: 500;
  src: url(./fonts/DrukWideCyr-Medium.8982444d.woff2) format("woff2");
  font-display: swap;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #000;
  color: #fff;
  font-family: Articul, Arial, sans-serif;
  font-size: 26px;
}

.page-wrapper {
  margin: 0 auto;
  padding: 0 60px;
  max-width: 1860px;
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  white-space: nowrap !important;
  clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  overflow: hidden !important;
}

.button {
  font-family: DrukWide, Arial, sans-serif;
  font-size: 28px;
  text-transform: uppercase;
  color: #fff;
  text-decoration: none;
  letter-spacing: 0.44px;
  cursor: default;
  padding: 20px;
  border-radius: 50px;
  transition: all 0.5s;
  position: relative;
}
.button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-color: #c31e54;
  border-radius: 50px;
  transition: all 0.3s;
}
.button::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: all 0.3s;
  border: 2px solid #c31e54;
  border-radius: 50px;
  transform: scale(1.2, 1.2);
}

@media (hover: hover) {
  .button:hover::before {
    opacity: 0;
    transform: scale(0.5, 0.5);
  }
  .button:hover::after {
    opacity: 1;
    transform: scale(0.99, 1);
  }
}
@media (max-width: 1220px) {
  .page-wrapper {
    padding: 0 33px;
  }
}
@media (max-width: 768px) {
  .page-wrapper {
    padding: 0 16px;
  }
}
.accordion__item {
  border: 3px solid #fff;
  border-left: 0;
  border-right: 0;
}
.accordion__item:not(:first-of-type) {
  border-top: 0;
}
.accordion__header {
  margin: 0;
}
.accordion__btn {
  position: relative;
  background-color: transparent;
  border: 0;
  padding-right: 100px;
  width: 100%;
  font-size: clamp(40px, 3.64vw, 70px);
  text-align: left;
  text-transform: uppercase;
  color: #fff;
}
.accordion__btn:before, .accordion__btn:after {
  content: "";
  height: 4px;
  width: 50px;
  background-color: #fff;
  transition: transform 0.3s ease-out;
  position: absolute;
  top: calc(50% - 2px);
  right: 0;
}
.accordion__btn.collapsed:before {
  transform: rotate(-180deg);
}
.accordion__btn.collapsed:after {
  transform: rotate(-90deg);
}
.accordion__collapse {
  transition: all 0.3s ease-out;
  overflow: hidden;
  box-shadow: #fff 0px -3px 0px 0px;
  max-height: 0;
}

@media (max-width: 768px) {
  .accordion__btn {
    font-size: clamp(20px, 8.54vw, 40px);
  }
  .accordion__btn:before, .accordion__btn:after {
    content: "";
    height: 3px;
    width: 44px;
  }
}
@media (max-width: 360px) {
  .accordion__btn {
    padding-right: 35px;
  }
  .accordion__btn:before, .accordion__btn:after {
    content: "";
    height: 2px;
    width: 21px;
  }
}
.career-count {
  font-family: DrukWide, Arial, sans-serif;
  position: relative;
  width: clamp(136px, 18.22vw, 350px);
  aspect-ratio: 1;
}
.career-count__count {
  position: absolute;
  inset: 0;
}
.career-count__count:after {
  content: attr(data-count);
  position: absolute;
  line-height: 1;
  font-size: clamp(52px, 6.77vw, 130px);
  top: 50%;
  left: 50%;
  translate: -50% -50%;
}
.career-count__caption {
  fill: currentColor;
  color: #c31e54;
  height: auto;
  max-width: 66vmin;
  transform-origin: center;
  width: 100%;
  animation: 30s linear infinite spin;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media (max-width: 768px) {
  .career-count {
    width: 182px;
  }
}
@media (max-width: 360px) {
  .career-count {
    width: 136px;
  }
}
.close {
  position: absolute;
  width: 90px;
  aspect-ratio: 1;
}
.close::before {
  content: "";
  display: block;
  width: 100%;
  height: 6px;
  background-color: #fff;
  transform: rotate(45deg);
  transition: all 0.3s ease-out;
  position: absolute;
  top: calc(50% - 3px);
}
.close:after {
  content: "";
  display: block;
  width: 100%;
  height: 6px;
  background-color: #fff;
  transform: rotate(-45deg);
  transition: all 0.3s ease-out;
  position: absolute;
  top: calc(50% - 3px);
}

@media (hover: hover) {
  .close:hover::before {
    background-color: #c31e54;
    transform: rotate(-135deg);
  }
  .close:hover::after {
    background-color: #c31e54;
    transform: rotate(135deg);
  }
}
@media (max-width: 768px) {
  .close {
    width: 34px;
  }
  .close::before {
    height: 4px;
    top: calc(50% - 2px);
  }
  .close:after {
    height: 4px;
    top: calc(50% - 2px);
  }
}
@media (max-width: 360px) {
  .close {
    width: 22px;
  }
  .close::before {
    height: 2px;
    top: calc(50% - 1px);
  }
  .close:after {
    height: 2px;
    top: calc(50% - 1px);
  }
}
.hand {
  transform: rotate(-90deg);
  width: clamp(262px, 26vw, 500px);
  height: clamp(262px, 26vw, 500px);
}
.hand__item:first-child {
  transform: rotate(136.52deg);
  margin: 0px 0 clamp(40px, 4.16vw, 80px) 8px;
}

.highlited {
  font-family: DrukWide, Arial, sans-serif;
  width: fit-content;
  font-size: clamp(10.7px, 1.16vw, 22.3px);
  border: 3px solid #c31e54;
  border-radius: 50px;
  padding: clamp(8px, 0.93vw, 18px);
  margin: clamp(8px, 0.78vw, 15px);
  text-transform: uppercase;
  white-space: nowrap;
  letter-spacing: 0.8px;
}

@media (max-width: 360px) {
  .highlited {
    border-width: 2px;
  }
}
.job {
  position: relative;
}
.job__body {
  max-width: 900px;
  margin: 0 auto;
  padding: 70px 0;
}
.job__experience {
  display: flex;
  flex-direction: column;
  line-height: 0.7;
  position: absolute;
  left: 0;
  font-size: clamp(20px, 1.19vw, 23px);
}
.job__experience span:first-child {
  margin-bottom: 1.56vw;
  font-size: 5.4em;
}
.job__description {
  font-size: 30px;
  line-height: 130%;
}
.job__tabs__nav {
  display: flex;
  justify-content: space-between;
  gap: 20px;
  margin: 100px 0 90px 0;
  max-width: 100vw;
  overflow: auto;
}
.job__tabs__link {
  font-family: DrukWide, Arial, sans-serif;
  text-transform: uppercase;
  font-size: 22px;
  line-height: 2;
  color: rgba(255, 255, 255, 0.8);
  letter-spacing: 0.44px;
}
.job__qualification {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 70px;
}
.job__qualification__item {
  max-width: calc(50% - 40px);
  padding: 24px 0;
  color: rgba(255, 255, 255, 0.8);
  border-top: 2px solid #303030;
  width: 100%;
}
.job__button {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 21px;
  width: 100%;
  margin-top: 73px;
}
.job__button__icon {
  width: 30px;
}

@media (max-width: 1200px) {
  .job__experience {
    position: relative;
  }
  .job__description {
    font-size: 22px;
    display: flex;
    justify-content: space-between;
    gap: 30px;
    flex-wrap: wrap;
  }
  .job__description p {
    max-width: 336px;
  }
}
@media (max-width: 768px) {
  .job__body {
    max-width: none;
  }
  .job__experience {
    font-size: clamp(16px, 1.04vw, 20px);
  }
  .job__tabs__nav {
    margin: 90px 0 65px 0;
  }
  .job__tabs__link {
    font-size: 18px;
  }
  .job__button {
    gap: 15px;
    margin-top: 58px;
    font-size: clamp(13.255px, 0.69vw, 22px);
    padding: clamp(10px, 0.88vw, 17px);
  }
  .job__qualification {
    gap: 40px;
  }
  .job__qualification__item {
    font-size: 18px;
  }
}
@media (max-width: 360px) {
  .job__description {
    font-size: 22px;
  }
  .job__description p {
    max-width: none;
  }
  .job__tabs__nav {
    margin: 50px 0 40px 0;
  }
  .job__tabs__link {
    font-size: 12px;
  }
  .job__tabs__link:nth-child(2) {
    order: 2;
  }
  .job__qualification {
    flex-direction: column;
    gap: 20px;
  }
  .job__qualification__item {
    max-width: none;
    font-size: 16px;
  }
}
.tabs__link {
  color: rgba(255, 255, 255, 0.8);
  cursor: default;
}
.tabs__link.active, .tabs__link:hover {
  color: #fff;
  border-bottom: 3px solid #fff;
}
.tabs__pane {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  background: rgba(255, 255, 255, 0.1);
  transition: 0;
}
.tabs__pane.active {
  position: relative;
  visibility: visible;
  background: transparent;
  opacity: 1;
  z-index: 5;
}

.careers__title {
  font-family: DrukWide, Arial, sans-serif;
  text-transform: uppercase;
  padding-top: 33px;
  padding-bottom: 33px;
}

@media (max-width: 768px) {
  .careers__title {
    font-size: clamp(20px, 5.2vw, 40px);
  }
}
@media (max-width: 360px) {
  .careers__title {
    padding-top: 17px;
    padding-bottom: 17px;
  }
}
.footer {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 120px 0;
}
.footer__arrow {
  width: clamp(255px, 27.6vw, 530px);
  height: clamp(220px, 23.8vw, 457px);
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
}
.footer__arrow__item {
  font-size: clamp(14px, 1.16vw, 22.3px);
  padding: clamp(12px, 0.93vw, 18px);
  margin: min(0.78vw, 15px);
}
.footer__arrow__item:nth-child(1) {
  transform: rotate(45deg) translate(60%, -280%);
}
.footer__arrow__item:nth-child(3) {
  transform: rotate(-45deg) translate(50%, 230%);
}
.footer__description {
  font-family: DrukWide, Arial, sans-serif;
  text-transform: uppercase;
  max-width: 800px;
  margin-left: 166px;
  line-height: 1.4;
  letter-spacing: 0.7px;
  font-size: clamp(25px, 1.82vw, 35px);
}
.footer__description__link {
  text-decoration: none;
  color: #fff;
  padding-bottom: 7px;
  background: linear-gradient(to right, #fff, #fff), linear-gradient(to right, rgb(255, 0, 0), rgb(255, 0, 180), rgb(0, 100, 200));
  background-size: 100% 3px, 0 3px;
  background-position: 100% 100%, 0 100%;
  background-repeat: no-repeat;
  transition: background-size 0.3s;
}

@media (hover: hover) {
  .footer__description__link:hover {
    background-size: 0 3px, 100% 3px;
  }
}
@media (max-width: 768px) {
  .footer {
    flex-direction: column;
    align-items: initial;
    gap: 80px;
  }
  .footer__arrow__item {
    font-size: 11px;
    padding: 13px;
    margin: 0px;
  }
  .footer__description {
    font-size: clamp(18px, 3.25vw, 25px);
    letter-spacing: 0.5px;
    margin: 0;
    width: 100%;
  }
}
@media (max-width: 360px) {
  .footer__description {
    letter-spacing: 0.36px;
  }
}
.header {
  position: relative;
  margin-bottom: 130px;
}
.header__close {
  position: fixed;
  top: 59px;
  right: 49px;
  z-index: 100;
}
.header__logo {
  margin-left: min(3.12vw, 60px);
}
.header__title {
  display: flex;
  align-items: flex-end;
  flex-wrap: wrap;
  line-height: 1;
}
.header__title h1 {
  font-family: DrukWide, Arial, sans-serif;
  text-transform: uppercase;
  font-size: min(8.75vw, 168px);
  letter-spacing: 3.367px;
  font-weight: 500;
}
.header__title h1:last-child {
  color: #c31e54;
}
.header__caption {
  max-width: 870px;
  margin: 0 auto;
  line-height: 1.3;
  margin-top: 65px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
.header__caption p {
  max-width: 500px;
}
.header__career-count {
  position: absolute;
  right: 65px;
  bottom: -40px;
}

@media (max-width: 1220px) {
  .header__title {
    justify-content: center;
  }
  .header__logo {
    margin-left: 0;
  }
  .header__caption {
    gap: 20px;
  }
  .career-count {
    position: relative;
    right: 0;
    bottom: 0;
  }
}
@media (max-width: 768px) {
  .header__title {
    justify-content: center;
  }
  .header__caption {
    font-size: 22px;
  }
  .header__caption p {
    max-width: 336px;
  }
  .header__close {
    top: 5dvh;
    right: 5dvh;
  }
}
@media (max-width: 600px) {
  .header {
    margin-bottom: 45px;
  }
  .header__caption {
    font-size: 16px;
    align-items: flex-end;
    flex-direction: column-reverse;
    row-gap: 46px;
  }
  .header__caption p {
    max-width: 100%;
  }
}
@media (max-width: 360px) {
  .header__close {
    top: 4dvh;
    right: 4dvh;
  }
}